/*******************************************************************************
 * General
 ******************************************************************************/

:root {
  --color-content: #fff;
  --color-footer: #000;
  --color-highlight: var(--color-brand-primary);
  --color-primary: #0797e1;
  --color-primary-hover: #0789ca;
  --color-secondary: #fafafa;
  --margin: 60px;
  font-size: 16px;
}

body {
  margin: 0;
}

a {
  color: var(--color-link);
}

a.button {
  display: inline-block;
  padding: 1em 2.5em;
  border: 3px solid var(--color-content);
  border-radius: 0.5em;
  font-weight: 600;
  color: var(--color-content);
  background-color: var(--color-primary);
  text-transform: uppercase;
  text-decoration: none;
  transition: all 200ms;
}

a.button:hover {
  background-color: var(--color-primary-hover);
}

a.button.secondary {
  border-color: var(--color-primary);
  color: var(--color-primary);
  background-color: var(--color-content);
}

a.button.secondary:hover {
  border-color: var(--color-primary-hover);
  color: var(--color-content);
  background-color: var(--color-primary-hover);
}

h1,
h2 {
  margin: 0 0 1rem;
}

h1 {
  font-size: 60px;
  line-height: 70px;
}

h2 {
  font-size: 30px;
  line-height: 40px;
}

h2.icon::before {
  display: inline-block;
  width: 23px;
  height: 23px;
  vertical-align: middle;
  background-size: 100%;
  content: "";
}

html[dir="ltr"] h2.icon::before {
  margin-right: 10px;
}

html[dir="rtl"] h2.icon::before {
  margin-left: 10px;
}

.block-icon {
  --icon-width: 40px;
  position: relative;
}

html[dir="ltr"] .block-icon {
  padding-left: calc(var(--icon-width) + 20px);
}

html[dir="rtl"] .block-icon {
  padding-right: calc(var(--icon-width) + 20px);
}

.block-icon::before {
  position: absolute;
  width: var(--icon-width);
  height: var(--icon-width);
  background-size: 100%;
  background-repeat: no-repeat;
  content: "";
}

html[dir="ltr"] .block-icon::before {
  left: 0;
}

html[dir="rtl"] .block-icon::before {
  right: 0;
}

/*******************************************************************************
 * Header
 ******************************************************************************/

header p {
  margin: 1.5em 0;
  color: #5a5a5a;
}

header .block-icon {
  --icon-width: 50px;
}

header a.logo {
  display: inline-block;
  margin-bottom: 70px;
}

header a.logo img {
  width: 200px;
}

#hero {
  flex: 0;
}

html[dir="ltr"] #hero {
  margin-left: 40px;
}

html[dir="rtl"] #hero {
  margin-right: 40px;
}

/*******************************************************************************
 * Footer
 ******************************************************************************/

footer {
  padding: 30px;
  font-size: 0.8rem;
  text-align: center;
  color: var(--color-content);
  background-color: var(--color-footer);
}

#copyright-notice a {
  font-weight: 600;
  text-decoration: none;
  color: inherit;
}

#copyright-notice a:hover {
  text-decoration: underline;
}

/*******************************************************************************
 * Content
 ******************************************************************************/

.content {
  position: relative;
  box-sizing: border-box;
  max-width: 940px;
  margin: auto;
  padding: var(--margin) 0 50px;
}

.content:not(:first-child) {
  padding-top: 0;
}

.content.columns {
  display: flex;
}

.content p {
  margin-top: 0;
  line-height: 26px;
}

.content.columns > div {
  display: flex;
  flex-direction: column;
  flex: 1;
  align-items: flex-start;
  justify-content: center;
}

.content.columns > div:last-child {
  align-items: flex-end;
}

@media (max-width: 960px) {
  .content.columns {
    flex-direction: column;
  }

  .content.columns > div:last-child {
    margin-top: 1em;
    align-items: flex-start;
  }
}

/*******************************************************************************
 * Sections
 ******************************************************************************/

article {
  position: relative;
}

article.primary,
article:last-child {
  margin-bottom: var(--margin);
}

article.primary::after,
article.secondary::before,
article:last-child::after {
  position: absolute;
  width: 100%;
  height: var(--margin);
  content: "";
}

article.primary::after,
article:last-child::after {
  top: 100%;
}

article.secondary::before {
  top: calc(var(--margin) * -1);
}

:not(.columns) > section:not(:first-of-type) {
  margin-top: 75px;
}

article.primary {
  margin-top: calc(100px + var(--margin));
  color: var(--color-content);
  background-color: var(--color-primary);
}

article.primary::after {
  background-image: linear-gradient(
    to bottom right,
    var(--color-primary) 49%,
    var(--color-content) 50%
  );
}

article.secondary {
  background-color: var(--color-secondary);
}

article.secondary::before {
  background-image: linear-gradient(
    to bottom right,
    var(--color-content) 49%,
    var(--color-secondary) 50%
  );
}

article:last-child::after {
  background-image: linear-gradient(
    to bottom right,
    var(--color-content) 49%,
    var(--color-footer) 50%
  );
}

/*******************************************************************************
 * Social
 ******************************************************************************/

#social .content {
  max-width: 230px;
  text-align: center;
}

#social p {
  color: #5a5a5a;
}

#social a {
  display: inline-block;
  width: 3em;
  height: 3em;
  margin: 1em 10px 0;
  border-radius: 1.5em;
  background-color: #000;
  background-position: center;
  background-repeat: no-repeat;
}

#social a.email {
  background-image: url(/skin/icons/email-white.svg);
}

#social a.facebook {
  background-image: url(/skin/icons/facebook-white.svg);
}

#social a.twitter {
  background-image: url(/skin/icons/twitter-white.svg);
}
